<template>
  <div class='container'>
    <el-button class="add-button" @click="$router.push('new')"  type="success" size="small" icon="el-icon-edit">新增试题</el-button>
    <!-- 顶部输入框 -->
    <el-form ref="form" :inline="true" :model="formData" style="overflow: hidden;" label-width="80px">
      <el-form-item label="学科" class="el-col el-col-6" >
         <el-select v-model="formData.subjectID" placeholder="请选择" @change="changeSubject">
            <el-option v-for="(item,index) in subject" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="二级目录" class="el-col el-col-6">
           <el-select v-model="formData.secondSubject" placeholder="请选择">
              <el-option v-for="(item,index) in secondSubject" :key="index" :label="item.label" :value="item.value"></el-option>
            </el-select>
        </el-form-item>
      <el-form-item label="标签" class="el-col el-col-6">
           <el-select v-model="formData.label" placeholder="请选择" @visible-change="clickTags">
              <el-option v-for="(item,index) in tags" :key="index" :label="item.label" :value="item.value"></el-option>
            </el-select>
        </el-form-item>
      <el-form-item label="关键字" class="el-col el-col-6">
        <el-input v-model="formData.keyWord"></el-input>
      </el-form-item>
      <el-form-item label="试题类型" class="el-col el-col-6">
          <el-select v-model="formData.questionType" placeholder="请选择">
            <el-option label="单选" value="1"></el-option>
            <el-option label="多选" value="2"></el-option>
            <el-option label="简答" value="3"></el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="难度" class="el-col el-col-6">
          <el-select v-model="formData.difficulty" placeholder="请选择">
            <el-option label="简单" value="1"></el-option>
            <el-option label="一般" value="2"></el-option>
            <el-option label="困难" value="3"></el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="方向" class="el-col el-col-6">
          <el-select v-model="formData.direction" placeholder="请选择">
            <el-option label="o2o" value="o2o"></el-option>
            <el-option label="外包服务" value="外包服务"></el-option>
            <el-option label="企业服务" value="企业服务"></el-option>
            <el-option label="互联网金融" value="互联网金融"></el-option>
            <el-option label="企业咨询" value="企业咨询"></el-option>
            <el-option label="互联网" value="互联网"></el-option>
            <el-option label="电子商务" value="电子商务"></el-option>
            <el-option label="其他" value="其他"></el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="录入人" class="el-col el-col-6">
            <el-select v-model="formData.creatorID" placeholder="请选择" @visible-change="clickUsers">
              <el-option v-for="(item,index) in usersArr" :key="index" :label="item" :value="item"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="题目备注" class="el-col el-col-6">
          <el-input v-model="formData.remarks"></el-input>
        </el-form-item>
        <el-form-item label="企业简称" class="el-col el-col-6">
          <el-input v-model="formData.shortName"></el-input>
        </el-form-item>
        <el-form-item label="城市" class="el-col el-col-6 city">
             <el-select v-model="formData.province" placeholder="请选择" @change="changeProvince" style="margin-right: 5px;">
                <el-option v-for="(item,index) in provincesArr" :key="index" :label="item" :value="item"></el-option>
              </el-select>
             <el-select v-model="formData.city" placeholder="请选择">
                <el-option v-for="(item,index) in cityArr" :key="index" :label="item" :value="item"></el-option>
              </el-select>
          </el-form-item>
        <el-form-item  class="el-col el-col-6 submit-item">
          <el-button @click="clear" >清除</el-button>
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
    </el-form>
    <!-- tabs栏 -->
     <el-row >
        <el-col :span="24">
          <el-tabs v-model="activeName" class="tableItem" @tab-click="changeTabs">
                <el-tab-pane label="全部" name="first"></el-tab-pane>
                <el-tab-pane label="待审核" name="0"></el-tab-pane>
                <el-tab-pane label="已审核" name="1"></el-tab-pane>
                <el-tab-pane label="已拒绝" name="2"></el-tab-pane>
              </el-tabs>
        </el-col>
      </el-row>
         <el-alert
         show-icon
          type="info">
          数据一共{{ questions.counts }}条
        </el-alert>
      <QuestionsTable></QuestionsTable>
      <!-- 分页 -->
       <el-pagination
       background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20, 50]"
        layout="total, sizes, prev, pager, next, jumper"
        :page-size="pagesize"
        :total="questions.counts"
        >
      </el-pagination>

  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import { provinces, citys } from '@/api/hmmm/citys.js'
import QuestionsTable from '../components/questions-choice-table.vue'
export default {
  components: {
    QuestionsTable
  },
  data () {
    return {
      formData: {
        subjectID: '',
        secondSubject: '',
        label: '',
        keyWord: '',
        questionType: '',
        difficulty: '',
        creatorID: '',
        remarks: '',
        shortName: '',
        provinces: '',
        city: ''
      },
      subjectValue: '',
      subjectArr: this.subject,
      secondSubjectArr: [],
      tagsArr: [],
      usersArr: [],
      provincesArr: [],
      cityArr: [],
      activeName: 'first',
      currentPage: 1,
      pagesize: 5,
      questionsArr: {},
      showPreview: false
    }
  },
  methods: {
    ...mapActions('questionsChoice/article', ['getSubjectAsync', 'getSecondSubjectAsync', 'getTagsAsync', 'getUsersAsync', 'getQuestionsAsync', 'getChoiceAsync']),
    ...mapMutations('questionsChoice/article', ['setPageSize', 'setPage']),
    // 改变学科事件
    async changeSubject (val) {
      this.formData.secondSubject = ''
      this.formData.label = ''
      // console.log(val)
      await this.getSecondSubjectAsync({ subjectID: val })
      await this.getTagsAsync({ subjectID: val })
    },
    changeProvince (val) {
      // console.log(val)
      this.cityArr = citys(val)
    },
    // 切换导航条
    async changeTabs () {
      if (this.activeName === 'first') {
        await this.getQuestionsAsync({
          page: this.currentPage,
          pagesize: this.pagesize
        })
      } else {
        await this.getQuestionsAsync({
          page: this.currentPage,
          pagesize: this.pagesize,
          chkState: this.activeName
        })
      }
    },
    // 改变当前页码事件
    async handleCurrentChange (val) {
      this.currentPage = val
      this.setPage(val)
      if (this.activeName === 'first') {
        await this.getQuestionsAsync(this._.pickBy({
          ...this.page,
          subjectID: this.formData.subjectID,
          catalogID: this.formData.secondSubject
        }))
      } else {
        await this.getQuestionsAsync(this._.pickBy({
          ...this.page,
          chkState: this.activeName,
          subjectID: this.formData.subjectID,
          catalogID: this.formData.secondSubject
        }))
      }
    },
    // 改变每页显示条数事件
    async handleSizeChange (val) {
      // console.log(val)
      this.pagesize = val
      this.setPageSize(val)
      this.getQuestionsAsync({
        ...this.page

      })
    },
    clickSubject () {
      this.subjectArr = this.getSubject.subject
      // console.log(this.subjectArr)
    },
    clickSecondSubject () {
      this.secondSubjectArr = this.getSubject.secondSubject
      // console.log(this.secondSubjectArr)
    },
    clickTags () {
      this.tagsArr = this.getSubject.tags
      // console.log(this.tagsArr)
    },
    clickUsers () {
      this.usersArr = this.getSubject.users.map(item => item.username)
      // console.log(this.usersArr)
    },
    // 清空搜索条件
    clear () {
      this.formData = {
        subjectID: '',
        secondSubject: '',
        label: '',
        keyWord: '',
        questionType: '',
        difficulty: '',
        creatorID: '',
        remarks: '',
        shortName: '',
        provinces: '',
        city: ''
      }
      this.getQuestionsAsync({
        ...this.page
      })
    },
    // 搜索
    async search () {
      if (this.formData.subjectID === '' && this.formData.secondSubject === '') {
        this.getQuestionsAsync({
          ...this.page
        })
      } else {
        this.getQuestionsAsync(this._.pickBy({
          ...this.page,
          subjectID: this.formData.subjectID,
          catalogID: this.formData.secondSubject
        }))
      }
    },
    // 预览
    preview (id) {
      // console.log(id)
      this.showPreview = true
      this.getChoiceAsync({ id })
    },
    setQuestionsArr () {
      // console.log(123321)
      this.questionsArr = this.getSubject.questions
    }
  },
  async created () {
    this.getSubjectAsync()
    // this.subjectArr = this.getSubject.subject
    await this.getUsersAsync()
    // this.usersArr = this.getSubject.users.map(item => item.username)
    this.provincesArr = provinces()
    await this.getQuestionsAsync({
      page: this.currentPage,
      pagesize: this.pagesize
    })
  },
  computed: {
    ...mapState('questionsChoice/article', ['subject', 'secondSubject', 'tags', 'users', 'questions', 'randomsDetail', 'page'])
  }

}
</script>

<style scoped lang='scss'>
::v-deep .el-pagination{
  // height: 500px;
  height: 40px;
  text-align: right;
}

  .container {
    overflow: hidden;
    padding: 20px;
    background-color: #fff;
    .add-button {
      float: right;
    }
    ::v-deep .el-form {
      margin-top: 47px;
      .el-form-item {
        margin-right: 0;
      }
       .el-input__inner {
        width: 282px;
      }
      .city{
        .el-input__inner {
          width: 135px;
        }
      }
      .submit-item {
        display: flex;
        justify-content: end;
      }
    }
  }
</style>
